<template><section><h1>CylinderGraphics</h1>
<p><code v-pre>vc-graphics-cylinder</code> 组件用于加载圆柱体、圆锥、圆锥台。需要作为 <code v-pre>vc-entity</code> 的子组件使用。</p>
<h2>示例</h2>
<h3>添加圆柱（锥）体到场景</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity ref="entity1" :position="position1" :description="description" :cylinder.sync="cylinder1">
          <vc-graphics-cylinder :length="400000.0" :topRadius="200000.0" :bottomRadius="200000.0" :material="material1" :outline="true" :outlineColor="outlineColor1"></vc-graphics-cylinder>
        </vc-entity>
        <vc-entity ref="entity2" :position="position2" :description="description" :cylinder.sync="cylinder2">
          <vc-graphics-cylinder :length="400000.0" :topRadius="0.0" :bottomRadius="200000.0" :material="material2"></vc-graphics-cylinder>
        </vc-entity>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"entity1"</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"position1"</span> <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">:cylinder.sync</span>=<span class="hljs-string">"cylinder1"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-cylinder</span>
          <span class="hljs-attr">:length</span>=<span class="hljs-string">"400000.0"</span>
          <span class="hljs-attr">:topRadius</span>=<span class="hljs-string">"200000.0"</span>
          <span class="hljs-attr">:bottomRadius</span>=<span class="hljs-string">"200000.0"</span>
          <span class="hljs-attr">:material</span>=<span class="hljs-string">"material1"</span>
          <span class="hljs-attr">:outline</span>=<span class="hljs-string">"true"</span>
          <span class="hljs-attr">:outlineColor</span>=<span class="hljs-string">"outlineColor1"</span>
        &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-cylinder</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"entity2"</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"position2"</span> <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">:cylinder.sync</span>=<span class="hljs-string">"cylinder2"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-cylinder</span> <span class="hljs-attr">:topRadius</span>=<span class="hljs-string">"0.0"</span> <span class="hljs-attr">:bottomRadius</span>=<span class="hljs-string">"200000.0"</span> <span class="hljs-attr">:material</span>=<span class="hljs-string">"material2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-cylinder</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">description</span>: <span class="hljs-string">'Hello Vue Cesium'</span>,
        <span class="hljs-attr">cylinder1</span>: {},
        <span class="hljs-attr">position1</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">105.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">200000.0</span> },
        <span class="hljs-attr">outlineColor1</span>: <span class="hljs-string">'DARK_GREEN'</span>,
        <span class="hljs-attr">material1</span>: {},

        <span class="hljs-attr">cylinder2</span>: {},
        <span class="hljs-attr">position2</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">110.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">200000.0</span> },
        <span class="hljs-attr">material2</span>: <span class="hljs-string">'RED'</span>
      }
    },
    mounted() {
      <span class="hljs-built_in">Promise</span>.all([<span class="hljs-keyword">this</span>.$refs.entity1.createPromise, <span class="hljs-keyword">this</span>.$refs.entity2.createPromise]).then(<span class="hljs-function">(<span class="hljs-params">instances</span>) =&gt;</span> {
        instances[<span class="hljs-number">0</span>].viewer.zoomTo(instances[<span class="hljs-number">0</span>].viewer.entities)
      })
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">this</span>.material1 = Cesium.Color.GREEN.withAlpha(<span class="hljs-number">0.5</span>)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定 cylinder 是否显示。</td>
</tr>
<tr>
<td>length</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> 指定 cylinder 的长。</td>
</tr>
<tr>
<td>topRadius</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 cylinder 的顶部半径。</td>
</tr>
<tr>
<td>bottomRadius</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 cylinder 的底部半径。</td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 cylinder 高度模式。 <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>fill</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定 cylinder 是否填充材质。</td>
</tr>
<tr>
<td>material</td>
<td>Object|String|Array</td>
<td><code v-pre>'WHITE'</code></td>
<td><code v-pre>optional</code> 指定 cylinder 的材质。</td>
</tr>
<tr>
<td>outline</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定 cylinder 是否绘制轮廓线。</td>
</tr>
<tr>
<td>outlineColor</td>
<td>Object|String|Array</td>
<td><code v-pre>'BLACK'</code></td>
<td><code v-pre>optional</code> 指定 cylinder 轮廓线颜色。</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code> 指定 cylinder 轮廓线宽度。</td>
</tr>
<tr>
<td>numberOfVerticalLines</td>
<td>Number</td>
<td><code v-pre>16</code></td>
<td><code v-pre>optional</code> 指定沿轮廓线周长绘制的垂直线数。</td>
</tr>
<tr>
<td>slices</td>
<td>Number</td>
<td><code v-pre>128</code></td>
<td><code v-pre>optional</code> 指定 cylinder 边节点数量。</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 cylinder 是否投射或接收每个点光源的阴影。 <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3, NUMBER_OF_SHADOW_MODES: 4, RECEIVE_ONLY: 3</strong></td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 cylinder 随相机距离显示条件。 <strong>结构：{ near: number, far: number }</strong></td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/CylinderGraphics.html">CylinderGraphics</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>每当更改或修改属性或子属性时触发该事件。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data () {
        return {
          description: 'Hello Vue Cesium',
          cylinder1: {},
          position1: { lng: 105.0, lat: 40.0, height: 200000.0 },
          outlineColor1: 'DARK_GREEN',
          material1: {},

          cylinder2: {},
          position2: { lng: 110.0, lat: 40.0, height: 200000.0 },
          material2: 'RED'
        }
      },
      mounted() {
        Promise.all([this.$refs.entity1.createPromise, this.$refs.entity2.createPromise]).then(
          (instances) => {
            instances[0].viewer.zoomTo(instances[0].viewer.entities)
          }
        )
      },
      methods: {
        ready (cesiumInstance) {
          const {Cesium, viewer} = cesiumInstance
          this.material1 = Cesium.Color.GREEN.withAlpha(0.5)
        }
      }
    }
  </script>